// src/components/gravity/PathSplit.tsx
"use client";

import * as React from "react";

export function PathSplit() {
  return (
    <section className="rounded-3xl border border-white/10 bg-white/5 p-7 sm:p-8">
      <div className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
        choose your path
      </div>

      <div className="mt-3 text-lg font-semibold tracking-tight">
  What are you trying to build?
</div>

<div className="mt-2 text-sm text-[var(--grav-dim)] leading-relaxed max-w-3xl">
  If you want a clean site and you already know exactly what it should say,
  you’ll move faster with a normal studio.
  <br />
  If you’ve got a product idea and you need it to become real, that’s my lane.
</div>


      <div className="mt-7 grid grid-cols-1 md:grid-cols-2 gap-4">
      <PathCard
  title="Already defined"
  body="You’ve got the copy, the structure, and the vibe locked. You mainly need execution."
  action="Find a studio"
  href="https://www.google.com/search?q=web+design+studio+sunshine+coast"
/>

<PathCard
  title="Still forming"
  body="You’ve got the idea, but the shape isn’t clear yet. You want to build it and discover it."
  action="Talk to me"
  href="#contact"
  accent
/>

      </div>
    </section>
  );
}

function PathCard(props: {
  title: string;
  body: string;
  action: string;
  href: string;
  accent?: boolean;
}) {
  const { title, body, action, href, accent } = props;

  return (
    <a
      href={href}
      className={[
        "block rounded-3xl border p-6 transition-all",
        accent
          ? "border-[var(--grav-accent)]/30 bg-[var(--grav-accent)]/10 hover:bg-[var(--grav-accent)]/14"
          : "border-white/10 bg-black/10 hover:bg-black/15",
      ].join(" ")}
    >
      <div className="flex items-center justify-between">
        <div className="text-sm font-semibold tracking-tight">{title}</div>
        <span className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
          {accent ? "signal" : "standard"}
        </span>
      </div>
      <div className="mt-2 text-sm leading-relaxed text-[var(--grav-dim)]">
        {body}
      </div>

      <div className="mt-5 inline-flex items-center gap-2 text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-ink)]/80">
        <span className="h-1 w-1 rounded-full bg-[var(--grav-accent)]" />
        {action}
      </div>
    </a>
  );
}
