// src/components/gravity/ArtifactStrip.tsx
"use client";

import * as React from "react";

type Artifact = {
  title: string;
  note: string;
  tag: string;
};
const ARTIFACTS: Artifact[] = [
    {
      title: "A weird onboarding that actually works",
      note: "One question. Two choices. Then you’re inside. No tour. No waffle.",
      tag: "onboarding",
    },
    {
      title: "A dashboard that feels calm",
      note: "Not more charts. Better grouping. Better defaults. Less noise.",
      tag: "ui systems",
    },
    {
      title: "A mobile flow you can do one-handed",
      note: "Thumb zones, big targets, no tiny text, no dead ends.",
      tag: "mobile",
    },
    {
      title: "A prototype in a weekend",
      note: "Real screens. Real data shape. Enough to test the idea properly.",
      tag: "prototype",
    },
  ];
  
export function ArtifactStrip() {
  return (
    <div className="rounded-3xl border border-white/10 bg-white/5 p-6 sm:p-7">
      <div className="flex items-center justify-between">
        <div className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
          artifacts
        </div>
        <div className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
          no gallery
        </div>
      </div>

      <div className="mt-4 space-y-3">
        {ARTIFACTS.map((a) => (
          <div
            key={a.title}
            className="rounded-2xl border border-white/10 bg-black/10 p-4 hover:bg-black/15 transition-colors"
          >
            <div className="flex items-center justify-between gap-3">
              <div className="text-sm font-semibold tracking-tight">{a.title}</div>
              <span className="text-[9px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
                {a.tag}
              </span>
            </div>
            <div className="mt-1 text-xs leading-relaxed text-[var(--grav-dim)]">
              {a.note}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
